.hero {
    width: 100%; /* Use full width */
    padding: 80px 8%;
    padding-top: 120px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; /* Allow wrapping */
}
.hero-btns{
    display: flex;
    align-items: center;
    gap:20px;
}
.hero-info {
    width: 55%;
    position: relative;
    overflow: hidden;
}
.hero-info h1{
    font-size: 3.2rem;
    margin-bottom: 20px;
}
.hero-info h1 span{
    color:var(--main-color);
    filter:drop-shadow(0 0 1px var(--main-color));
    position: relative;
    -webkit-filter:drop-shadow(0 0 1px var(--main-color));
}
.hero-info p {
    font-size: 25px;
    margin-bottom: 20px;
    font-weight: 400;
    display: inline-block;
    white-space: nowrap;
    animation: slideLeft 10s linear infinite;
    position: relative;
}
.social-icons{
    margin-bottom: 30px;
    display: flex;
    gap:20px;
    align-items: center;
    margin-left: 5px;
}
.social-icons i{
    height: 50px;
    width: 50px;
    font-size: 28px;
    border-radius: 50%;
    border:1px solid var(--main-color);
    box-shadow: 0 0 10px var(--main-color);
    color:var(--main-color);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .6s;
}
.social-icons i:hover{
    box-shadow: 0 5px 16px var(--main-color);
    color:#fff;
    transform: translateY(-5px);
    background-color: var(--main-color);
}
.hero-img {
    width: 45%;
    display: flex;
    justify-content: end;
    align-items: center;
    border-radius: 50%;
    transition: .6s;
}
.hero-img img {
    background-color: #6262ff;
    width: 25vw;
    height: 25vw;
    border-radius: 50%;
    object-fit: cover;
    transition: .6s;
    border: 1px solid var(--main-color);
    box-shadow: 0 0 10px var(--main-color);
}
.hero-img img:hover{
    box-shadow: 0 0 80px var(--main-color);
    transform: translateY(-15px);
}

@keyframes slideLeft {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
}

@media (max-width: 950px) {
    .hero {
        flex-direction: column; /* Stack elements on smaller screens */
    }
    .hero-info {
        width: 100%; /* Full width on small screens */
        margin-bottom: 50px;
    }
    .hero-img {
        width: 100%; /* Full width on small screens */
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .hero-img img {
        width: 70vw; /* Responsive width */
        height: auto; /* Maintain aspect ratio */
    }
}

@media (max-width: 550px) {
    .hero-info h1 {
        font-size: 2.5rem; /* Smaller font size for very small screens */
    }
    .hero-info p {
        font-size: 20px; /* Smaller font size for very small screens */
    }
    .hero-img img {
        width: 90vw; /* Responsive width */
        height: auto; /* Maintain aspect ratio */
    }
}